.App{
  display: flex;
 background-color: rgb(33, 47, 47); 
 height: 100vh;
 width: 100vw;
 overflow: scroll;
}
a {
  text-decoration: none;
}
a:visited {
  color: unset;
}
#cars {
  position: fixed;
  width: 92%;
  transform: translate(-4%, 103%) translateZ(-250px) perspective(1200px) rotate(2deg) rotateY(-30deg);
  /* animation: flicker 0.12s infinite; */
  transition: 0.2s ease all;
  display: flex;
  /* transform-style: preserve-3d; */
  /* transform: translate(-50%); */
}
#tv-wrapper {
  position: relative;
  left: 50%;
  transform: translate(-30%,-60%);
  width: 45%;
  min-width: 600px;
  max-width: 625px;
  height: fit-content;
  top: 30%;
}
#tv {
  /* position: relative; */
  width: 100%;
  height: 100%;
  margin: auto;
  filter: drop-shadow(2px 5px 5px rgba(0, 0, 0, 0.464));
  z-index: inherit;
}

@keyframes flicker {
  0%, 100% {
    filter:brightness(107%) hue-rotate(10deg) blur(0.1px);
  }
  30% {
    filter: brightness(100%) blur(0.1px);
  }
}


#remote {
  position: absolute;
  bottom: 5%;
  left: 35%;
  width: 20vw;
  min-width: 250px;
  max-width: 225px;
  height: fit-content;
  margin: auto;
  /* transform: translate(-50%, -50%); */
  filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.532))
}
#remote-buttons {
  position: absolute;
  height: 58%;
  width:35%;
  top: 11%;
  left: 34%;
  transform: translate(-0.2vw) perspective(1000px) rotate(22deg) rotateY(-35deg) rotateX(25deg);
  justify-content: space-evenly;
  display: flex;
  flex-wrap: wrap;
}
#remote-buttons .tv-button {
  position: relative;
  width: 28%;
  height: 10%;
  cursor: pointer;
  padding: 5%;
  border: none;
  outline: 0;
  opacity: 0;
}
#remote-buttons .tv-button:active {
  opacity: 0.1;
  filter: drop-shadow(1px 1px 1px black);
}
#go-button {
  cursor: pointer;
  transform: perspective(500px) rotate(-4deg) rotateY(15deg);
  padding: 0;
  background-color: greenyellow;
  border-radius: 0.4rem;
  height: 15%;
  outline: none;
  font-size: 0.8rem;
  font-weight: 800;
}
#go-button:active {
  background-color: rgb(205, 255, 131);
}
#repair-shop-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  backdrop-filter: blur(6px);
  z-index: 100;
  flex-direction: row;
}
#repair-shop-wrapper p {
  width: 70%;
  height: fit-content;
  margin: auto;
  padding: 7rem 4rem;
  background-color: rgba(0, 0, 0, 0.677);
  color: rgb(255, 213, 0);
  font-weight: 800;
  text-align: center;
  border-radius: 1.5rem;
  box-shadow: 0px 0px 50px 5px rgb(255, 187, 0);
  line-height: 3rem;
  font-size: 2vw;
  display: flex;
  flex-direction: row;
}
#repair-shop-wrapper span {
  height: 2rem;
  line-height: 1rem;
}
#github-logo {
  width: 8%;
  height: 8%;
  z-index: 1000 !important;
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 20%;
  bottom: 1%;
}
#github-logo > img {
  width: 100%;
  height: 100%;
  position: relative;
  transform: rotate(5deg) perspective(25vw) rotateY(25deg);
}
#github-logo > img:hover { 
  filter: drop-shadow(0px 0px 2px rgba(55, 255, 55, 0.498));
}
@media only screen and (max-width: 850px) {
  #tv-wrapper {
    width: 70vw;
    min-width: 300px;
  }
  #remote {
    left: unset;
  }
  #repair-shop-wrapper {
    flex-direction: column;
    margin: auto;
  }
#repair-shop-wrapper p {
  flex-direction: column;
  width: 90% !important;
  max-width: 90% !important;
  padding: 0%;
  font-size: larger;
  line-height: 4vw;
}
#repair-shop-wrapper p div{
height: 10rem;
line-height: 5vw !important;

}

#repair-shop-wrapper p img {
  margin: auto;
  padding: 2rem;
  height: fit-content !important;
  width: 50vw!important;
}
}